.page {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 100%;
  display: flex;
}

.page__header {
  background: linear-gradient(177deg, #00000080 0, #0003 5rem, #fff0 10rem);
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
  min-height: 10rem;
  padding: .75rem 1rem;
  display: flex;
}

@media screen and (width >= 576px) {
  .page__header {
    flex-direction: row;
    justify-content: space-between;
  }
}

.page__logo {
  text-transform: uppercase;
  letter-spacing: .2rem;
  flex: none;
  width: 100%;
  font: bold 2rem / 3rem RubikMonoOne, sans-serif;
}

